import React, { useState } from "react";
import { observer } from "mobx-react-lite";
import { makeStyles } from "@material-ui/core/styles";
import {
    Container,
    Card,
    CardActionArea,
    CardActions,
    CardContent,
    Typography,
    IconButton,
    CardMedia,
    Grid,
    Box,
    Paper,
    List,
    ListItem,
    ListItemAvatar,
    Avatar,
    ListItemText,
    Divider,
    ListItemSecondaryAction,
    Button,
    Tabs,
    Tab,
    Chip,
} from "@material-ui/core";
import { SkipNext, PlayArrow, Pause, Queue } from "@material-ui/icons";
import { useStores } from "../stores";

const useStyles = makeStyles((theme) => ({
    container: { flex: "1 1 auto", overflow: "hidden" },
    box: {
        height: "100%",
        overflow: "hidden",
        paddingTop: theme.spacing(3),
    },
    paper: { padding: theme.spacing(3), height: "100%", overflow: "hidden" },
    songspaper: {
        padding: theme.spacing(3),
        height: "100%",
        overflow: "hidden",
        display: "flex",
        flexDirection: "column",
    },
    root: {
        display: "flex",
        flexGrow: "1",
    },
    details: {
        display: "flex",
        flex: "1 1 auto",
        flexDirection: "column",
    },
    content: {
        flex: "1 0 auto",
    },
    cover: {
        width: 151,
    },
    controls: {
        display: "flex",
        alignItems: "center",
        paddingLeft: theme.spacing(1),
        paddingBottom: theme.spacing(1),
    },
    playIcon: {
        height: 38,
        width: 38,
    },
    gridRoot: {
        height: "100%",
        flexWrap: "unset",
    },
    flexshrink: {
        flex: "0 0 auto",
    },
    playlistscontainer: {
        overflow: "hidden",
        flex: "1 1 auto",
    },
    playlists: {
        width: "100%",
        height: "100%",
        overflow: "scroll",
    },
    playlist: {
        cursor: "pointer",
        "&:hover": {
            background: theme.palette.background.default,
        },
    },
    songs: {
        overflow: "scroll",
        width: "100%",
        flex: "1 1 auto",
    },
    tabs: {
        flex: "0 0 auto",
    },
    song: {
        cursor: "pointer",
        "&:hover": {
            background: theme.palette.background.default,
        },
    },
    card: {
        background: "#f0f0f0",
        height: "100%",
    },
    memories: {
        overflowY: "auto",
        marginTop: ".5rem",
    },
    image: {
        height: "10rem",
    },
    selectedSong: { marginTop: ".5rem" },
    selectedSongText: { marginRight: ".5rem" },
}));

const MediaControlCard = observer(() => {
    const classes = useStyles();

    const {
        spotifyStore: { paused, current_song, togglePlayback, nextSong },
    } = useStores();

    return !current_song ? null : (
        <Card className={classes.root}>
            <div className={classes.details}>
                <CardContent className={classes.content}>
                    <Typography component="h5" variant="h5">
                        {current_song?.title || ""}
                    </Typography>
                    <Typography variant="subtitle1" color="textSecondary">
                        {current_song?.artists?.join(", ") || ""}
                    </Typography>
                </CardContent>
                <div className={classes.controls}>
                    <IconButton
                        aria-label="play/pause"
                        disabled={!current_song}
                        onClick={togglePlayback}
                    >
                        {paused ? (
                            <PlayArrow className={classes.playIcon} />
                        ) : (
                            <Pause className={classes.playIcon} />
                        )}
                    </IconButton>
                    <IconButton
                        aria-label="next"
                        disabled={!current_song}
                        onClick={nextSong}
                    >
                        <SkipNext />
                    </IconButton>
                </div>
            </div>
            <CardMedia
                className={classes.cover}
                image={current_song?.img || ""}
                title="Album cover"
            />
        </Card>
    );
});

const Playlists = observer(() => {
    const {
        spotifyStore: { playlists, setPlaylist },
    } = useStores();

    const classes = useStyles();

    return (
        <Paper className={classes.paper}>
            <List className={classes.playlists}>
                {playlists.map((playlist, index) => (
                    <div key={playlist?.id || index}>
                        <ListItem
                            alignItems="flex-start"
                            className={classes.playlist}
                        >
                            <ListItemAvatar>
                                <Avatar alt="" src={playlist?.img || ""} />
                            </ListItemAvatar>
                            <ListItemText
                                primary={playlist?.title || ""}
                                secondary={<>{playlist?.owner || ""}</>}
                            />
                            <ListItemSecondaryAction>
                                <Button
                                    color="primary"
                                    onClick={() => setPlaylist(playlist?.id)}
                                >
                                    Select
                                </Button>
                            </ListItemSecondaryAction>
                        </ListItem>
                        <Divider variant="inset" component="li" />
                    </div>
                ))}
            </List>
        </Paper>
    );
});

const Memories = observer(() => {
    const {
        applicationStore: {
            memories,
            deleteMemory,
            selectedSong,
            setSelectedSong,
        },
        spotifyStore: { current_song },
    } = useStores();

    const classes = useStyles();

    const song_memories = memories.filter(
        (memory) => memory.song_id === (selectedSong?.id || current_song?.id)
    );

    return (
        <div>
            <div>
                {selectedSong ? (
                    <Grid container className={classes.selectedSong}>
                        <Typography
                            variant="h5"
                            className={classes.selectedSongText}
                        >
                            Memories for selected song:{" "}
                        </Typography>
                        <Chip
                            color="secondary"
                            label={selectedSong?.title || "Selected song"}
                            avatar={<Avatar>S</Avatar>}
                            onDelete={() => {
                                setSelectedSong();
                            }}
                        />
                    </Grid>
                ) : (
                    <Typography
                        variant="h5"
                        className={classes.selectedSong}
                    >{`Memories for current song: ${
                        current_song?.title || "-"
                    }`}</Typography>
                )}
            </div>
            <Grid container spacing={2} className={classes.memories}>
                {song_memories.map((memory, index) => (
                    <Grid key={memory?.id || index} item xs={4}>
                        <Card className={classes.card}>
                            <CardActionArea>
                                <CardMedia
                                    className={classes.image}
                                    square
                                    image={memory?.downloadUrl || ""}
                                    title="Memory"
                                />
                                <CardContent>
                                    <Typography
                                        gutterBottom
                                        variant="h5"
                                        component="h2"
                                    >
                                        {memory?.user || "-"}
                                    </Typography>
                                    <Typography
                                        variant="body2"
                                        color="textSecondary"
                                        component="p"
                                    >
                                        {memory?.text || "-"}
                                    </Typography>
                                </CardContent>
                            </CardActionArea>
                            <CardActions>
                                <Button
                                    size="small"
                                    color="primary"
                                    onClick={() => deleteMemory(memory?.pk)}
                                >
                                    Remove
                                </Button>
                            </CardActions>
                        </Card>
                    </Grid>
                ))}
            </Grid>
        </div>
    );
});

const Songs = observer(({ onSelectSong }) => {
    const {
        applicationStore: { memories, setSelectedSong },
        spotifyStore: { songs, playSong, queueSong },
    } = useStores();

    const classes = useStyles();

    return (
        <List className={classes.songs}>
            {songs.map((song, index) => (
                <div key={song?.id || index}>
                    <ListItem alignItems="flex-start" className={classes.song}>
                        <ListItemAvatar>
                            <Avatar alt="" src={song?.img || ""} />
                        </ListItemAvatar>
                        <ListItemText
                            primary={song?.title || ""}
                            secondary={<>{song?.artists?.join(", ") || ""}</>}
                        />
                        <ListItemSecondaryAction>
                            <Chip
                                color="secondary"
                                label={`${
                                    memories?.filter(
                                        (memory) => memory?.song_id === song?.id
                                    )?.length
                                }`}
                                onClick={() => {
                                    setSelectedSong(song?.id);
                                    onSelectSong();
                                }}
                            />
                            <IconButton
                                aria-label="play"
                                onClick={() => playSong(song?.uri)}
                            >
                                <PlayArrow />
                            </IconButton>
                            <IconButton
                                aria-label="queue"
                                onClick={() => queueSong(song?.uri)}
                            >
                                <Queue />
                            </IconButton>
                        </ListItemSecondaryAction>
                    </ListItem>
                    <Divider variant="inset" component="li" />
                </div>
            ))}
        </List>
    );
});

const SongsAndMemories = observer(() => {
    const {
        spotifyStore: { playlist },
    } = useStores();

    const classes = useStyles();
    const [currentTab, setCurrentTab] = useState(0);

    return (
        <Paper className={classes.songspaper}>
            <Typography component="h5" variant="h5">
                {`${playlist?.title || ""}`}
            </Typography>
            <Tabs
                className={classes.tabs}
                value={currentTab}
                onChange={(_, tab) => setCurrentTab(tab)}
                aria-label="tabs"
                variant="fullWidth"
            >
                <Tab label="Play or queue songs" id="songs" />
                <Tab label="Memories" id="memories" />
            </Tabs>
            {currentTab === 0 && (
                <Songs onSelectSong={() => setCurrentTab(1)} />
            )}
            {currentTab === 1 && <Memories />}
        </Paper>
    );
});

const Host = observer(() => {
    const {
        spotifyStore: { playlist_id },
    } = useStores();

    const classes = useStyles();

    return (
        <Container className={classes.container}>
            <Box className={classes.box}>
                <Grid
                    container
                    spacing={2}
                    direction="column"
                    className={classes.gridRoot}
                >
                    <Grid item xs={12} className={classes.flexshrink}>
                        <MediaControlCard />
                    </Grid>
                    <Grid item xs={12} className={classes.playlistscontainer}>
                        {playlist_id ? <SongsAndMemories /> : <Playlists />}
                    </Grid>
                </Grid>
            </Box>
        </Container>
    );
});

export default Host;
