import React, { useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import {
    Container,
    Grid,
    Box,
    List,
    ListItem,
    ListItemAvatar,
    Avatar,
    ListItemText,
    ListItemSecondaryAction,
    IconButton,
    Divider,
    Paper,
    Typography,
    DialogTitle,
    DialogContent,
    TextField,
    DialogActions,
    Button,
    Dialog,
} from "@material-ui/core";
import { RateReview } from "@material-ui/icons";
import { observer } from "mobx-react-lite";
import { DropzoneArea } from "material-ui-dropzone";

import { useStores } from "../stores";
import { useScreenSize } from "../hooks/screensize";

const useStyles = makeStyles((theme) => ({
    "@global": {
        ".dropzone": {
            height: "10rem",
            minHeight: "unset",
        },
        // ".dropzone .MuiTypography-root": { margin: "unset" },
        // ".dropzone .MuiSvgIcon-root MuiDropzoneArea-icon": { display: "none" },
        ".dropzone .MuiDropzonePreviewList-root": {
            marginLeft: "-28px",
            marginTop: "-119px",
        },
    },
    container: {
        flex: "1 1 auto",
        overflow: "hidden",
    },
    box: {
        height: "100%",
        overflow: "hidden",
        paddingTop: theme.spacing(3),
    },
    gridRoot: {
        height: "100%",
        flexWrap: "unset",
    },
    songscontainer: {
        overflow: "hidden",
        flex: "1 1 auto",
    },
    songspaper: {
        padding: theme.spacing(3),
        height: "100%",
        overflow: "hidden",
        display: "flex",
        flexDirection: "column",
    },
    songs: {
        overflow: "scroll",
        width: "100%",
        flex: "1 1 auto",
    },
    song: {
        cursor: "pointer",
        "&:hover": {
            background: theme.palette.background.default,
        },
    },
    noplaylist: {
        height: "100%",
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
    },
    dialog: {
        [theme.breakpoints.up("sm")]: {
            width: "34rem",
        },
    },
    dialogcontent: {
        height: "23rem",
    },
    dropzone: {
        height: "6rem",
    },
}));

const Songs = observer(() => {
    const {
        applicationStore: { openAddMemoryPopup },
        spotifyStore: { songs, playlist },
    } = useStores();

    const classes = useStyles();

    return (
        <List className={classes.songs}>
            <Typography component="h5" variant="h5">
                {`${playlist?.title || ""}`}
            </Typography>
            {songs.map((song, index) => (
                <div key={song?.id || index}>
                    <ListItem alignItems="flex-start" className={classes.song}>
                        <ListItemAvatar>
                            <Avatar alt="" src={song?.img || ""} />
                        </ListItemAvatar>
                        <ListItemText
                            primary={song?.title || ""}
                            secondary={<>{song?.artists?.join(", ") || ""}</>}
                        />
                        <ListItemSecondaryAction>
                            <IconButton
                                aria-label="add-memory"
                                onClick={() => openAddMemoryPopup(song?.id)}
                            >
                                <RateReview />
                            </IconButton>
                        </ListItemSecondaryAction>
                    </ListItem>
                    <Divider variant="inset" component="li" />
                </div>
            ))}
        </List>
    );
});

const NoPlaylist = () => {
    const classes = useStyles();

    return (
        <div className={classes.noplaylist}>
            <Typography variant="subtitle1">
                The host is still choosing a playlist
            </Typography>
        </div>
    );
};

const AddMemoryDialog = observer(() => {
    const {
        applicationStore: { selectedSong, closeAddMemoryPopup, addMemory },
    } = useStores();

    const classes = useStyles();
    const screensize = useScreenSize();

    const [memory, setMemory] = useState({});

    const handleFiles = async (files) => {
        const [file] = files;
        if (!file) return;
        setMemory({ ...memory, file });
    };

    return (
        <Dialog
            onClose={() => {}}
            open
            fullScreen={screensize === "mobile"}
            classes={{ paper: classes.dialog }}
        >
            <DialogTitle>
                Add memory to song: {selectedSong?.title || ""}
            </DialogTitle>
            <DialogContent className={classes.dialogcontent}>
                <Grid container spacing={2}>
                    <Grid item xs={12}>
                        <TextField
                            fullWidth
                            variant="outlined"
                            multiline
                            rows={7}
                            label={"Memory"}
                            onChange={(e) => {
                                setMemory({
                                    ...memory,
                                    text: `${e.target.value}`.substr(0, 300),
                                });
                            }}
                            value={memory?.text || ""}
                        />
                    </Grid>
                </Grid>
                <Grid container spacing={2}>
                    <Grid item xs={12}>
                        <DropzoneArea
                            dropzoneClass="dropzone"
                            onChange={handleFiles}
                            filesLimit={1}
                            acceptedFiles={["image/*"]}
                            dropzoneText="Upload an image"
                        />
                    </Grid>
                </Grid>
            </DialogContent>
            <DialogActions>
                <Grid container spacing={2}>
                    <Grid item xs={6}>
                        <Button
                            fullWidth
                            size={"medium"}
                            color={"secondary"}
                            variant={"contained"}
                            onClick={closeAddMemoryPopup}
                        >
                            Cancel
                        </Button>
                    </Grid>
                    <Grid item xs={6}>
                        <Button
                            fullWidth
                            disabled={
                                !memory?.file || memory?.text?.length < 10
                            }
                            size={"medium"}
                            color={"primary"}
                            variant={"contained"}
                            onClick={() => {
                                addMemory(memory);
                                closeAddMemoryPopup();
                            }}
                        >
                            Send
                        </Button>
                    </Grid>
                </Grid>
            </DialogActions>
        </Dialog>
    );
});

const Guest = observer(() => {
    const {
        applicationStore: { popup },
        spotifyStore: { songs },
    } = useStores();

    const classes = useStyles();

    return (
        <>
            {popup === "addMemory" && <AddMemoryDialog />}
            <Container className={classes.container}>
                <Box className={classes.box}>
                    <Grid
                        container
                        spacing={2}
                        direction="column"
                        className={classes.gridRoot}
                    >
                        <Grid item xs={12} className={classes.songscontainer}>
                            <Paper className={classes.songspaper}>
                                {songs?.length ? <Songs /> : <NoPlaylist />}
                            </Paper>
                        </Grid>
                    </Grid>
                </Box>
            </Container>
        </>
    );
});

export default Guest;
